.select {
  margin-top: 10px;
  outline: none;
  color: rgb(33, 33, 94, 0.8) !important;
  border-bottom: 5px solid rgb(33, 33, 94, 0.8);
}

.color-green {
  color: #3584ce;
}

.color-white {
  color: rgb(59, 59, 59);
}
li a {
  color:rgb(59, 59, 59);
  padding: 10px 0 13px 0;
  font-size: 18px;
  font-weight: 500;
}

li a:hover {
  color:rgb(33, 33, 94, 0.5);
  border-bottom: 5px solid rgb(33, 33, 94, 0.5);
}
